<template>
  <div id="Category">
    <category-top-tab />

    <div class="content">
      <tab-menu class="tab-menu" :categories="categories" @selectItem="selectItem"></tab-menu>
      <!-- <h2>132</h2> -->
      <category-detail
        :CategoryDetailData="CategoryDetailData"
      ></category-detail>
    </div>
  </div>
</template>

<script>
import brtterScroll from "@/components/common/scroll/brtterScroll.vue";
import CategoryTopTab from "./ChildrenComps/CategoryTopTab.vue";
import TabMenu from "./ChildrenComps/TabMenu.vue";
import CategoryDetail from "./ChildrenComps/CategoryDetail.vue";
import {
  getCategory,
  getSubcategory,
  getCategoryDetail,
} from "@/network/category.js";

export default {
  name: "Category",
  data() {
    return {
      categories: [],
      CategoryDetailData: [],
      datailIndex: 0,
      maitKey: 3627,
    };
  },
  components: {
    brtterScroll,
    CategoryTopTab,
    TabMenu,
    CategoryDetail,
  },
  methods: {
    selectItem(index) {
      this.datailIndex = index;
      console.log(this.datailIndex);

      this._quireCate();
    },
    _quireCate() {
      getCategory().then((res) => {
        this.categories = res.data.category.list;
        this.maitKey = res.data.category.list[this.datailIndex].maitKey;
        this._getSubcategory(this.maitKey);
      });
    },
    _getSubcategory(w) {
      getSubcategory(w).then((res) => {
        console.log(res.data.list);
        this.CategoryDetailData = res.data.list;
      });
    },
  },
  created() {
    this._quireCate();
    this._getSubcategory(this.maitKey);
  },
};
</script>

