<template>
  <brtter-scroll id="tab-menu" :probeType="3" :click="true" :pullUpLoad="true">
    <div class="menu-list">
      <div
        class="menu-list-item"
        v-for="(item, index) in categories"
        :class="{ active: index === currentIndex }"
        :key="index"
        @click="itemClick(index)"
      >
        {{item.title}}
        <!-- {{ item }} -->
      </div>
    </div>
  </brtter-scroll>
</template>

<script>
// import brtterScroll from 'components/common/brtterScroll/Scroll'
import brtterScroll from "@/components/common/scroll/brtterScroll.vue";


export default {
  name: "TabMenu",
  components: {
    brtterScroll,
  },
  props: {
    categories: Array,
  },
  data() {
    return {
      currentIndex: 0,
    };
  },
  methods: {
    itemClick(index) {
      this.currentIndex = index;
      this.$emit("selectItem", index);
    },
  },
};
</script>

<style scoped>
#tab-menu {
  background-color: #f6f6f6;
  height: 100%;
  width: 100px;
  box-sizing: border-box;
}

.menu-list-item {
  height: 45px;
  line-height: 45px;
  text-align: center;
  font-size: 14px;
}

.menu-list-item.active {
  font-weight: 700;
  color: var(--color-high-text);
  background-color: #fff;
  border-left: 3px solid var(--color-high-text);
}
</style>
